article {
  position: relative;
  padding-top: 70px;
  padding-bottom: 30px;
  padding-left: 5%; }

.parallax-window {
  min-height: 600px;
  background: transparent; }

.how__container {
  width: 100%;
  height: 100%; }

.article__header {
  margin: 0;
  padding: 0;
  display: block;
  text-align: center;
  border: 1px solid black;
  width: 350px;
  margin: 0 auto;
