.functions {
  height: 555px; }
  .functions::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 370px;
    bottom: 0;
    z-index: -1;
    left: 0%; }

.func__container {
  display: flex;
  flex-direction: row;
  justify-content: space-around; }

.func__list, .func__item {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 16px; }

.func__list {
  flex: 2 1 300px;
  text-align: right;
  margin-top: 50px;
  padding-top: 20px;
  padding-bottom: 25px;
  border-right: 3px solid black; }
  .func__list li {
    position: relative;
    text-transform: uppercase;
    margin-bottom: 20px;
    letter-spacing: 1px; }

.func__item {
  display: block;
  padding-right: 7%;
  color: black;
  font-weight: 600;
  margin-left: 15%;
  transition: background 1s; }
  .func__item::before {
    position: absolute;
    content: "";
    width: 93%;
    height: 9px;
    background: #ffeb3b;
    top: 26%;
    right: 4%;
    z-index: -1;
    transition: 0.5s; }
  .func__item a {
    text-decoration: none;
    color: black; }
  .func__item:hover {
    font-weight: 600;
    background: black;
    margin-left: 4%; }
    .func__item:hover::before {
      display: none;
      position: absolute;
      content: "";
      width: 93%;
      height: 9px;
      background: #ffeb3b;
      top: 25%;
      right: -7%;
      z-index: -1;
      transition: 0.5s; }
    .func__item:hover a {
      color: #ffeb3b; }

.func__item-active {
  font-weight: 600;
  background: black;
  margin-left: 4%; }
  .func__item-active::before {
    display: none; }
  .func__item-active a {
    color: #ffeb3b; }

.func__content, .func__descr {
  list-style: none;
  padding: 0;
  margin: 0; }

.func__content {
  flex: 3 1 400px;
  margin-top: 27px;
  margin-left: 5%;
  margin-right: 5%;
  position: relative; }

.func__descr {
  display: none;
  flex-direction: column;
  align-items: center;
  transition: all 1s; }
  .func__descr h3 {
    text-align: center;
    text-transform: uppercase;
    font-size: 32px;
    font-weight: 300; }
  .func__descr p {
    text-align: justify; }

.func__descr-active {
  display: flex;
  transition: all 1s; }

