//tools
@import "_misc/mixins";
@import "_misc/normalize";

//
@import "themes/classic";
//fonts
@import "../fonts/gilroy.css";
/*
	Gilroy, 300, 800
*/



* {
  @include box-sizing(border-box);

}

body {
  line-height: 1.42;
  position: relative;
  background-size: cover;
  font-family: Gilroy, Arial, sans-serif;
  color: black;
}

html {
  font-size: 16px;

  @include desktop {
    font-size: 16px;
  }

  @include laptop {
    font-size: 15px;
  }

  @include tablets {
    font-size: 14px;
  }

  @include phones {
    font-size: 13px;
  }
}

.wrapper {
  padding: 10px;
  padding-top: 40px;
  width: 1200px;

  margin: 0 auto;

  @include desktop {
    width: 970px;
  }

  @include laptop {
    width: 750px;

  }

  @include tablets {
    width: 480px;

  }

  @include phones {
    width: auto;
  }


}

.header{
  height: 300px;
  width: 100%;
  background: rgba(247,226,150,0.5);
  position: relative;
  }

.header__logo {
  position: absolute;
  left: 5%;
  top: -14%;
  & h1 {
    padding: 0;
    margin: 0;
    color: black;
    font-size: 60px;
    letter-spacing: 8px;
  }
}
.header_descr{
  & h2{
    position: absolute;
    color: #000;
    display: block;
    font-size: 19px;
    font-weight: 600;
    right: 5%;
    top: -16%;
    letter-spacing: 3px;
    width: 289px;

    &::before{
      position: absolute;
      content: "";
      width: 100%;
      height: 6px;
      background: rgba(255,255,0,0.6);
      top: 44%;
      right: 22%;
      z-index: -1;
    }
  }
}

.main__menu{
  position: absolute;
  top: 17%;
  left: -2%;
  width: 104%;
  height: 50px;
  background: rgb(229, 229, 229);
  box-shadow: 2px 2px 8px rgba(0,0,0,0.3);

  & ul{
    padding: 0;
    margin: 0;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    list-style: none;
    align-items: center;
  }
  & li{
    padding: 0;
    margin: 0;
    font-size: 21px;
    color: black;
  }
}

.header__contacts{
  position: absolute;
  width: 340px;
  top: 52%;
  left: 6%;
  font-size: 21px;
  text-align: left;
  font-weight: 600;
  animation-duration: 3s;
  animation-delay: 2s;

  & a{
    display: block;
    text-decoration: none;
    color: black;
    font-style: normal;
    letter-spacing: 1px;
  }
}

.bxslider, .slide{
  list-style: none;
  margin: 0;
  padding: 0;
  color: black;
}

.header__slider{
  position: absolute;
  width: 60%;
  top: 49%;
  right: 3%;
}

.bxslider li{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: stretch;
}

.slide{
  display: flex;
  flex: 1 1 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100px;
  font-size: 25px;
  color: black;


}

.bx-wrapper .bx-pager, .bx-pager-link {
  display: none;
}

.bx-pager-item {
  display: none;
}

article{
  position: relative;
  padding-top: 70px;
  padding-bottom: 30px;
  padding-left: 5%;
}

.parallax-window{
  min-height: 600px;
  background: transparent;
}

.how__container{
  width: 100%;
  height: 100%;
}

.article__header{
  margin: 0;
  padding: 0;
  display: block;
  text-align: center;
  border: 1px solid black;
  width: 350px;
  margin: 0 auto;
  text-transform: uppercase;
  letter-spacing: 6px;
}

.how__descr{
  width: 600px;
  text-align: justify;
  margin-top: 45px;
  padding: 10px;
  background: rgba(255,255,255,0.7);
}

.rollIn{
  animation-duration: 3s;
  animation-delay: 2s;
}

//functions

.functions{
  height: 555px;

  &::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 370px;
    bottom: 0;
    z-index: -1;
    left: 0%;
  }
}

.func__container{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.func__list, .func__item {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 16px;
  }

.func__list{
  flex: 2 1 300px;
  text-align: right;
  margin-top: 50px;
  padding-top: 20px;
  padding-bottom: 25px;
  border-right: 3px solid black;

  & li{

    position: relative;
    text-transform: uppercase;
    margin-bottom: 20px;
    letter-spacing: 1px;

  }
}

.func__item {
  display: block;
  padding-right: 7%;
  color: black;
  font-weight: 600;
  margin-left: 15%;
  transition: background 1s;


  &::before, {
    position: absolute;
    content: "";
    width: 93%;
    height: 9px;
    background: #ffeb3b;
    top: 26%;
    right: 4%;
    z-index: -1;
    transition: 0.5s;
  }

  & a{
    text-decoration: none;
    color: black;
  }

  &:hover{
    font-weight: 600;
    background: black;
    margin-left: 4%;

    &::before {
      display: none;
      position: absolute;
      content: "";
      width: 93%;
      height: 9px;
      background: #ffeb3b;
      top: 25%;
      right: -7%;
      z-index: -1;
      transition: 0.5s;
    }

    & a{
      color: #ffeb3b;
    }

  }
}

.func__item-active {
  font-weight: 600;
  background: black;
  margin-left: 4%;

  &::before {
    display: none;

  }

  & a{
    color: #ffeb3b;
  }
}

.func__content, .func__descr{
  list-style: none;
  padding: 0;
  margin: 0;
}

.func__content{
  flex: 3 1 400px;
  margin-top: 27px;
  margin-left: 5%;
  margin-right: 5%;
  position: relative;
}

.func__descr{

  display: none;
  flex-direction: column;
  align-items: center;
  transition: all 1s;

  & h3{
    text-align: center;
    text-transform: uppercase;
    font-size: 32px;
    font-weight: 300;
  }

  & p{
    text-align: justify;
  }
}

.func__descr-active{
  display: flex;
  transition: all 1s;

}

.gsm {
  background: rgba(247,226,150,.5);
}

.gsm__container{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  text-align: justify;
}

.gsm__intro{
  flex: 1 1 100%;
}

.gsm__universal{
  flex: 1 1 200px;
  padding-right: 3%;
  border-right: 1px solid black;
}

.gsm__incoming{
  flex: 1 1 200px;
  padding-left: 3%;
}

.gsm__numbers{
  flex: 1 1 100%;
}

.gsm__intro{
  margin-top: 30px;
}

.gsm__title{
  text-align: center;
  text-transform: uppercase;
  border-top: 1px solid black;
  display: block;
  width: 63%;
  margin: 0 auto;
  margin-top: 20px;
  padding-top: 7px;
}

.gsm_3{
  margin-left: 37%;
  font-size: 22px;
  font-weight: 500;
}

.superprice{
  font-size: 20px;
}

.gsm__numbers{
  display: flex;
  flex-direction: column;
  margin-top: 15px;
}

.numbers_container{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  margin-top: -15px;
  margin-bottom: -15px;
}

.descr__oper{
  flex: 1 1 250px;
  height: 80px;
  margin-right: 10px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  & strong{
    margin-left: 10px;
    font-size: 17px;

  }
}

.logo_oper{
  width: 50px;
  height: 50px;
}